import { _decorator, Component, Node, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ScoreController')
export class ScoreController extends Component {

	@property({type: Label})
 	public grand: Label|null = null;

 	@property({type: Label})
 	public major: Label|null = null;

 	@property({type: Label})
 	public minor: Label|null = null;

 	@property({type: Label})
 	public mini: Label|null = null;

    start() {

    }

    update(deltaTime: number) {
        this.showScore
    }	

    showScore(){
        this.grand.string = cc.gb.gameEnv.totalPlayedPattern[cc.gb.user.totalPlayed];
        this.major.string = cc.gb.user.entireLevel;
        this.minor.string = cc.gb.user.ways;
        this.mini.string = cc.gb.user.coinValue;
    }	
    
}

